<template>
  <div>
    <layoutsHeader />
    <div class="container">
      <a href="#" @click="deleteWallet">Delete current wallet</a>
      <div class='hello' ref='chartdiv'></div>
    </div>
  </div>
</template>

<script>
import * as am4core from '@amcharts/amcharts4/core'
import * as am4charts from '@amcharts/amcharts4/charts'
import am4themesAnimated from '@amcharts/amcharts4/themes/animated'
import layoutsHeader from './layouts/header'

am4core.useTheme(am4themesAnimated)
export default {
  name: 'WalletComponent',
  components: {
    layoutsHeader
  },
  mounted () {
    this.$store.dispatch('getWallet')
  },
  watch: {
    '$store.state.wallet' (to, from) {
      this.initChart()
    }
  },
  methods: {
    initChart () {
      if (!this.$store.getters.getCurrentWallet(this.$route.params.idWallet)) return false
      if (!this.$refs[`chartdiv`]) return false
      let data = []
      let sumCoins = 0
      data.push({
        date: new Date(this.$store.getters.getCurrentWallet(this.$route.params.idWallet).created_at),
        name: 'name0',
        value: 0
      })
      if (this.$store.getters.getCurrentWallet(this.$route.params.idWallet).transaction) {
        this.$store.getters.getCurrentWallet(this.$route.params.idWallet).transaction.forEach((item, i) => {
          if (item.type === 'put' || item.type === 'delegate_from') sumCoins += parseInt(item.value)
          if (item.type === 'out' || item.type === 'delegate_to') sumCoins -= parseInt(item.value)
          data.push({
            date: new Date(item.created_at),
            name: 'name' + (i + 1),
            value: sumCoins
          })
        })
      }
      if (this.chart) {
        this.chart.data = data
        return false
      }
      let chart = am4core.create(this.$refs[`chartdiv`], am4charts.XYChart)
      chart.data = data
      let dateAxis = chart.xAxes.push(new am4charts.DateAxis())
      dateAxis.renderer.grid.template.location = 0
      let valueAxis = chart.yAxes.push(new am4charts.ValueAxis())
      valueAxis.tooltip.disabled = true
      valueAxis.renderer.minWidth = 35
      let series = chart.series.push(new am4charts.LineSeries())
      series.dataFields.dateX = 'date'
      series.dataFields.valueY = 'value'
      series.tooltipText = '{valueY.value} coins'
      chart.cursor = new am4charts.XYCursor()
      let scrollbarX = new am4charts.XYChartScrollbar()
      scrollbarX.series.push(series)
      chart.scrollbarX = scrollbarX
      this.chart = chart
    },
    deleteWallet () {
      this.$store.dispatch('deleteWallet', this.$route.params.idWallet).then(() => {
        this.$store.dispatch('getWallet')
        this.$router.push({name: 'account'})
      })
    }
  }
}
</script>

<style lang="css">
.hello{
  height: 500px;
  width: 100%;
}
</style>



