<template>
    <div>
      <layoutsHeader />
      <button @click='createWallet'>CREATE NEW WALLET</button>
      <div class="card-group">
        <div v-for="(wallet) in this.$store.getters.getWallet" :key="wallet.id">
          <router-link :to="{ name: 'wallet', params: {idWallet: wallet.id} }">
            <div class="card">
              <div class="card-body">
                <h5 class="card-title">{{wallet.hash}}</h5>
                <p class="card-text">Balance: <b>{{wallet.balance}}</b> coins</p>
                <p class="card-text">Balance delegate: <b>{{wallet.balance_delegate}}</b> coins</p>
              </div>
              <div class="card-footer">
                <small class="text-muted">{{wallet.created_at}}</small>
              </div>
            </div>
          </router-link>
        </div>
      </div>
    </div>
</template>

<script>
import layoutsHeader from './layouts/header'

export default {
  name: 'AccountComponent',
  components: {
    layoutsHeader
  },
  data () {
    return {
      idWalletDelete: 0
    }
  },
  mounted () {
    this.getWallet()
    this.chart = []
  },
  methods: {
    createWallet () {
      this.$store.dispatch('createWallet')
      this.getWallet()
    },
    getWallet () {
      this.$store.dispatch('getWallet')
    }
  }
}
</script>

<style scoped>
.hello{
  height: 500px;
  width: 80%;
}
.card {
  margin: 25px;
  text-align: left;
}
.card-group a{
  color: inherit;
  transition: .25s linear;
}
.card-group a:hover {
  text-decoration: none;
}
.card-group {
  margin: 50px 10px;
}
.card-group a:hover > div {
  transition: .2s linear;
  transform: scale(1.05, 1.05);
  box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
}
</style>



