import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import BootstrapVue from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

Vue.use(BootstrapVue)
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    user: {

    },
    wallet: [

    ],
    transactions: {

    }
  },
  getters: {
    getWallet (state) {
      return state.wallet
    },
    getCurrentWallet: state => idWallet => {
      let result = []
      if (state.wallet.length) {
        for (let item of state.wallet) {
          if (parseInt(item.id) === parseInt(idWallet)) {
            result = item
            break
          }
        }
      }
      return result
    },
    getTransactions (state) {
      return state.transactions
    }
  },
  mutations: {
    setWallet (state, wallet) {
      Vue.set(state, 'wallet', wallet)
    },
    pushTransactions (state, {transactions, idWallet}) {
      state.wallet.forEach(item => {
        if (!item.transaction) Vue.set(item, 'transaction', [])
        if (parseInt(idWallet[0]) === parseInt(item.id) || parseInt(idWallet[1]) === parseInt(item.id)) Vue.set(item.transaction, item.transaction.length, transactions)
      })
    }
  },
  actions: {
    // Wallet methods
    getWallet (store) {
      axios.get('wallet')
        .then(function (result) {
          let walletArray = []
          store.commit('setWallet', result.data.data.wallet)
          result.data.data.wallet.forEach((item) => {
            walletArray.push(item.id)
          })
          store.dispatch('getTransactions', walletArray)
        })
    },
    createWallet (store) {
      axios.post('wallet')
    },
    deleteWallet (store, idWallet) {
      axios.delete(`wallet/${idWallet}`)
    },
    // transactions methods
    getTransactions (store, idWallet) {
      axios.post(`wallet/transaction`, {
        wallets_id: idWallet
      }).then(function (result) {
        result.data.data.transaction.forEach(item => {
          store.commit('pushTransactions', {transactions: item, idWallet: [item.to, item.from]})
        })
      })
    },
    createTransaction (store) {
      axios.post('wallet')
    },
    deleteTransaction (store, idWallet) {
      axios.delete(`wallet/${idWallet}`)
    }
  }
})

export default store



