<template lang="html">
  <div>
    <layoutsHeader />
    <div class="container">
      <form class="form-signin">
        <img class="mb-4" src="/docs/4.3/assets/brand/bootstrap-solid.svg" alt="" width="72" height="72">
        <h1 class="h3 mb-3 font-weight-normal">Please sign in</h1>

        <label for="inputName" class="sr-only">Name</label>
        <input type="text" id="inputName" v-model="name" class="form-control" placeholder="Name" required autofocus>

        <label for="inputEmail" class="sr-only">Email address</label>
        <input type="email" id="inputEmail" v-model="email" class="form-control" placeholder="Email address" required>

        <label for="inputPassword" class="sr-only">Password</label>
        <input type="password" id="inputPassword" v-model="password" class="form-control" placeholder="Password" required>

        <label for="inputRepeatPassword" class="sr-only">Repeat password</label>
        <input type="password" id="inputRepeatPassword" v-model="RepeatPassword" class="form-control" placeholder="Repeat password" required>

        <button class="btn btn-lg btn-primary btn-block" type="submit" @click="register($event)">Sign up</button>
        <p class="mt-5 mb-3 text-muted">&copy; 2017-2019</p>
        <div class="alert alert-danger" role="alert" v-if="this.error">
          {{this.textError}}
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import layoutsHeader from './layouts/header'
export default {
  name: 'HomeComponent',
  components: {
    layoutsHeader
  },
  data () {
    return {
      name: '',
      email: '',
      password: '',
      RepeatPassword: '',
      error: false,
      textError: 'Invalid data'
    }
  },
  methods: {
    register () {
      event.preventDefault()
      this.$auth.register({
        data: {
          name: this.name,
          email: this.email,
          password: this.password,
          password_confirmation: this.RepeatPassword
        },
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        method: 'POST',
        fetchUser: false,
        autoLogin: true,
        redirect: false
      }).then(result => {
        this.$auth.token(result.data.token)
        this.$router.push({ name: 'account' })
        // eslint-disable-next-line
      }).catch(error => {
        this.error = true
      })
    }
  }
}
</script>

<style lang="css">
</style>



