<template>
  <div>
    <layoutsHeader />
        <div class="container">
          <form class="form-signin">
            <img class="mb-4" src="/docs/4.3/assets/brand/bootstrap-solid.svg" alt="" width="72" height="72">
            <h1 class="h3 mb-3 font-weight-normal">Please sign in</h1>
            <label for="inputEmail" class="sr-only">Email address</label>
            <input type="email" id="inputEmail" v-model="email" class="form-control" placeholder="Email address" required autofocus>
            <label for="inputPassword" class="sr-only">Password</label>
            <input type="password" id="inputPassword" v-model="password" class="form-control" placeholder="Password" required>
            <button class="btn btn-lg btn-primary btn-block" type="submit" @click="login($event)">Sign in</button>
            <p class="mt-5 mb-3 text-muted">&copy; 2017-2019</p>
            <div class="alert alert-danger" role="alert" v-if="this.error">
              {{this.textError}}
            </div>
          </form>
        </div>
    </div>
</template>

<script>
import layoutsHeader from './layouts/header'
export default {
  name: 'AuthComponent',
  components: {
    layoutsHeader
  },
  data () {
    return {
      email: '',
      password: '',
      error: false,
      textError: 'Invalid data'
    }
  },
  mounted () {
    if (this.$auth.check()) {
      this.$router.push({ name: 'account' })
    }
  },
  methods: {
    login (event) {
      event.preventDefault()
      this.$auth.login({
        data: {
          email: this.email,
          password: this.password
        },
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        method: 'POST',
        fetchUser: false,
        redirect: false
      }).then(result => {
        this.$auth.token(result.data.token)
        this.$router.push({ name: 'account' })
        // eslint-disable-next-line
      }).catch(error => {
        this.error = true
      })
    }
  }
}
</script>

<style scoped>
.container{
  width: 25%;
}
</style>



