<template lang="html">
  <nav class="navbar navbar-expand-lg navbar-light bg-light">
    <a class="navbar-brand" href="#">Blockchain</a>
    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarNav">
      <ul class="navbar-nav">
        <li :class="['nav-item', this.$route.name == 'account' ? 'active' : '']">
          <router-link :to="{ name: 'account' }" class="nav-link">Account</router-link>
        </li>
        <li :class="['nav-item', this.$route.name == 'login' ? 'active' : '']" v-if="!this.$auth.token()">
          <router-link :to="{ name: 'login' }" class="nav-link">Login</router-link>
        </li>
        <li :class="['nav-item', this.$route.name == 'register' ? 'active' : '']" v-if="!this.$auth.token()">
          <router-link :to="{ name: 'register' }" class="nav-link">Register</router-link>
        </li>
        <li class="nav-item" v-if="this.$auth.token()">
          <a class="nav-link" href="#" @click="logout">Logout</a>
        </li>
      </ul>
    </div>
  </nav>
</template>

<script>
export default {
  name: 'layoutsHeader',
  methods: {
    logout () {
      this.$auth.logout({
        makeRequest: true,
        redirect: false
      }).then(() => {
        this.$router.push({name: 'login'})
      })
    }
  }
}
</script>

<style lang="css">
</style>



